body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: rgb(14, 13, 13)
}
.mouseNotHover {
  opacity: 0.7;
  transition: opacity .7s ease-in-out;
}

.colorBackground {
  background: white;
  width: 57vw;
}

.mouseHover {
  opacity: 1;
  transition: opacity .7s ease-in-out;
}

.container-video{
  margin-left: 35vw;
  margin-top: 30px;
}

.navBar{
  position: fixed;
  width: 44px;
  height: 100vh;
  background: red;
  margin-top: -30px;
}

.about{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 50vh;
}

.texte{
  margin-left: 35vw;
  color: #F7C974;
}

.introVideo{
  max-width: 30vw;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  display: block;
  margin-left: auto;
  margin-right: auto;
  
}

.landing{
  height: 100vh;
  width: 100vw;
}

.logoWhite{
  width: 84px;
  position: absolute;
  margin-top: 20px;
  margin-left: 20px;
}

.titrePortfolio{
  position: absolute;
  color: white;
  right: 0;
  bottom: 0;
  padding: 20px;
  font-size: 40px
}

.nav{
  position: fixed;
  height: 100vh;
  width: 35px;
  background: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  transition: all .7s ease-in-out;
}


.hiden{
  position: fixed;
  height: 100vh;
  width: 0px;
  background: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  transition: all .7s ease-in-out;
}

.plusNav{
  margin-top: 50vh
}
